import React, { Component } from 'react';
import { connect } from 'react-redux';

import Aux from '../../hoc/Aux1.js';
import Burger from '../../components/Burger/Burger';
import BuildControls from '../../components/Burger/BuildControls/BuildControls';
import Modal from '../../components/UI/Modal/Modal';
import OrderSummary from '../../components/Burger/OrderSummary/OrderSummary';
import Spinner from '../../components/UI/Spinner/Spinner'
import axios from '../../axios-orders';
import orderSummary from '../../components/Burger/OrderSummary/OrderSummary';
import withErrorHandler from '../../hoc/withErrorHandler/withErrorHandler';
import * as actionType from '../../store/actions';


class BurgerBuilder extends Component{

  state = {
    totalPrice: 4,
    purchasable: false,
    purchasing: false,
    loading: false,
    error: false
  }

  // componentDidMount(){
  //   axios.get('/ingredients.json')
  //         .then(response => {
  //           this.setState({ingredients: response.data})
  //         })
  //         .catch(error => {
  //           this.setState({error: true});
  //         });
  // }

  updatePurchaseState(ingredients) {

    const sum = Object.keys(ingredients)
        .map(igKey => {
          return ingredients[igKey];
        }).reduce((sum, el) => {
          return sum + el;
        }, 0);

    return sum>0;
  }

  // addIngredientHandler = (type) => {
  //   const newCount = this.state.ingredients[type] + 1;
  //   const updateIngredients = {
  //         ...this.state.ingredients
  //       };
  //   updateIngredients[type] = newCount;
  //   const priceAdded = INGREDIENT_PRICES[type] ;
  //   const oldPrice = this.state.totalPrice ;
  //   const newPrice =  priceAdded + oldPrice ;
  //   this.setState({totalPrice: newPrice, ingredients: updateIngredients});
  //   this.updatePurchaseState(updateIngredients);

  // }

  // removeIngredienthandler = (type) => {
  //   const newCount = this.state.ingredients[type] - 1;
  //   if(newCount < 0)
  //     return;
  //   const updateIngredients = {
  //         ...this.state.ingredients
  //       };
  //   updateIngredients[type] = newCount;
  //   const priceDeducted = INGREDIENT_PRICES[type] ;
  //   const oldPrice = this.state.totalPrice ;
  //   const newPrice =  oldPrice - priceDeducted ;
  //   this.setState({totalPrice: newPrice, ingredients: updateIngredients});
  //   this.updatePurchaseState(updateIngredients);
  // }

  purchaseHandler = () => {
    this.setState({purchasing: true});
  };

  purchaseCancelHandler = () => {
    this.setState({purchasing: false});
  }

  purchaseContinueHandler = () => {
    // console.log(this.props);
    
    // const queryParams = [];

    // for(let i in this.props.ings){
    //   queryParams.push(encodeURIComponent(i) + '=' + encodeURIComponent(this.props.ings[i]))
    // }
    // queryParams.push(encodeURIComponent('price') + '=' + encodeURIComponent(this.props.price));
    // const queryString = queryParams.join('&');
    this.props.history.push('/checkout');
    
  }

  render(){
    const disabledInfo = {
      ...this.props.ings
    }

    for(let key in disabledInfo){
      disabledInfo[key] = disabledInfo[key] <= 0;
    }

    let orderSummary = null;

    let burger= this.state.error ? <p style={{textAlign: 'center'}}>Ingredients can't be loaded.</p> : <Spinner />;

    if(this.props.ings){
      burger = ( 
              <Aux>
              <Burger ingredients={this.props.ings} />
              <BuildControls
                ingredientAdded={this.props.onIngredientAdded}
                ingredientRemoved={this.props.onIngredientRemoved}
                disabled={disabledInfo}
                purchasable={this.updatePurchaseState(this.props.ings)}
                price={this.props.price}
                ordered={this.purchaseHandler} />
                </Aux>
      );

      orderSummary = (
                      <OrderSummary
                        ingredients={this.props.ings}
                        price={this.props.price}
                        cancelled={this.purchaseCancelHandler}
                        continue={this.purchaseContinueHandler}/>
      );
    }

    if(this.state.loading){
      orderSummary = <Spinner />
    }
    
    return (
      <Aux>
      <Modal show={this.state.purchasing} modalClosed={this.purchaseCancelHandler}>
        {orderSummary}
      </Modal>
      {burger}
      </Aux>
    );
  };

}

const mapStateToProps = state => {
  return {
      ings: state.ingredients,
      price: state.totalPrice
  };
}

const mapDispatchToProps = dispatch => {
  return {
      onIngredientAdded: (ingName) => dispatch({type: actionType.ADD_INGREDIENT, ingredientName: ingName}),
      onIngredientRemoved: (ingName) => dispatch({type: actionType.REMOVE_INGREDIENT, ingredientName: ingName})
  };
}

export default connect(mapStateToProps, mapDispatchToProps) (withErrorHandler(BurgerBuilder, axios));



