import React from 'react';

import Logo from '../../assets/Images/burger-logo.png.png';
import classes from './Logo.css';

const logo = (props) => (
    <div className={classes.Logo} style={{height: props.height}}>
      <img src={Logo} alt='MyBurger'/>
    </div>
);

export default logo;



